<template>
    <div>
    <button class="button-open" v-on:click="isHidden = !isHidden"></button>

        <div class="chat-container" id="myForm" v-if="!isHidden">
            <div class="chat-header">
              <p class="header-title">Welcome to Ooredoo Live Chat</p>
              <button type="button" class="button-chat-close" v-on:click="isHidden = true"> ✖ </button>
            </div>
            
            <div class="chat-inner">
              <form>
              <div class="row instructions">
                  <p>To start chatting, enter the following details and tap on Chat</p>
              </div>
              <div class="row row-input-name">
                  <input class="input-style" type="text" name="name" required placeholder="" pattern="\D*">
                  <label>Name</label>
                  <span class="focus-border">
                    <i></i>
                  </span>
              </div>

              <div class="row row-input-number">
                  <input class="input-style" type="number" name="number" required placeholder="">
                  <label>Number</label>
                  <span class="focus-border">
                    <i></i>
                  </span>
              </div>

              <div class="row row-input-email">
                  <input class="input-style" type="email" name="email" required placeholder="">
