                  <span class="focus-border">
                    <i></i>
                  </span>
              </div>
              
              <div class="row row-button-send">
                  <button type="submit" class="button-send" style="transform-origin: center center">CHAT</button>
              </div>
              
              </form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  data () {
   return {
     isHidden: true
   }
 }
}
</script>

<style lang="scss" scoped>

// BUTTONS
.button-open {
  background: transparent url(https://dimelo.s3.amazonaws.com/custom-v2/ooredoo-mv/chat/chat-icon_vertical-outline.png) no-repeat center center;
