}

.chat-header {
  position: relative;
  padding: 10px 35px;
  background-color: #eb212e;
  border-radius: 25px;
  color: #fff;
  margin: 5px;
  
}

.chat-header-title {
  font-size: 15px;
  margin: 0;
  padding: 2px;
}

.chat-inner {
  padding: 15px 35px 15px;
  display: inline-block;
}


.chat-container .row:not(.button-send) {
    margin: 20px 0 18px;
}

.chat-container .row input {
    //height: 32px;
    //border-radius: 20px;
    border-top: none;
    border-left: none;
