}

.chat-container .row {
  text-align: center;
  position: relative;
  //height: 32px;
  box-sizing: border-box;
  width: 230px;
}


/****  input border and label style 

 - https://codepen.io/maheshambure21/pen/EozKKy  

****/ 

.input-style{
  border-bottom:#eb212e; 
  border: 1px solid #ccc; 
  padding: 7px 14px; 
  transition: 0.4s; 
  background: transparent;
}

.input-style ~ .focus-border:before,
.input-style ~ .focus-border:after{
  content: ""; position: absolute; 
  top: 0; left: 0; width: 0; height: 2px; 
  background-color: #eb212e; transition: 0.3s;
}
.input-style ~ .focus-border:after{
  top: auto; bottom: 0; 
  left: auto; right: 0;
}

.input-style ~ .focus-border i:before,
.input-style ~ .focus-border i:after{
  content: ""; position: absolute; 
  top: 0; left: 0; width: 2px; 
  height: 0; background-color: #eb212e; 
  transition: 0.4s;
}

.input-style ~ .focus-border i:after{
  left: auto; right: 0; 
  top: auto; bottom: 0;
}

.input-style:focus ~ .focus-border:before,
.input-style:focus ~ .focus-border:after,
.has-content.input-style ~ .focus-border:before,
.has-content.input-style ~ .focus-border:after{
  width: 100%; 
  transition: 0.3s;
}
.input-style:focus ~ .focus-border i:before,
.input-style:focus ~ .focus-border i:after,
.has-content.input-style ~ .focus-border i:before,
.has-content.input-style ~ .focus-border i:after{
  height: 100%; 
  transition: 0.4s;
}

.chat-inner label {
  position: absolute; 
  left: 3px; top: 10px;
  width: 100%;  color: #aaa; 
  transition: 0.3s; z-index: -1; 
  letter-spacing: 0.5px;
}

.chat-inner input:focus ~ label, 
.chat-inner input:focus ~ label, 
.chat-inner input:valid ~ label {
  top: -24px;
  font-size: 15px;
  color: #eb212e;
  margin-top: 5px;
  //border-bottom: #aaa solid 1px;
}

input:focus {
  outline: none;
}

.instructions p{
  text-align: center;
}

input:-moz-placeholder {
    box-shadow:none !important;
}
/**** END input border and label style ****/


